export default {
	data() {
		return {
			type: '',
			imgUrl: 'http://106.15.194.58/images/', //图片接口
			detailList: [{
					name: '西红柿炒蛋',
					num: 2,
					cash: 15
				},
				{
					name: '西红柿炒蛋',
					num: 2,
					cash: 15
				},
				{
					name: '西红柿炒蛋',
					num: 2,
					cash: 15
				},
				{
					name: '西红柿炒蛋',
					num: 2,
					cash: 15
				},
				{
					name: '西红柿炒蛋',
					num: 2,
					cash: 15
				},
				{
					name: '西红柿炒蛋',
					num: 2,
					cash: 15
				},
				{
					name: '西红柿炒蛋',
					num: 2,
					cash: 15
				}
			],
			money: 0,
			orderNum: 0,
			peopleNum: 0,
			userName: '',
			mobile: '',
			remarks: '',
			orderId: '',
			makeTime: '',
			orderDetails: [{
					name: '订单号',
					value: '1321 1478 1478 4874 788'
				},
				{
					name: '用餐人数',
					value: '便于商家携带',
				},
				{
					name: '联系人',
					value: '张女士 13656564898',
				},
				{
					name: '订单备注',
					value: '多放辣椒少放盐多放辣椒少放盐多放辣椒少放盐多放辣椒少放盐',
				}
			]
		}
	},
	onLoad(e) {
		this.orderId = e.id;
		console.log(e)
		this.type = e.type
		this.getOrderDetail(e.id);
	},
	methods: {
		confirmMeal() { // 确认取餐
			///foods/shopcar/accept
			let that = this
			if (this.type == 2) {
				uni.request({
					url: this.nowUrl + '/foods/shopcar/accept?id=' + this.orderId,
					method: 'POST',
					header: {
						"content-type": "application/json",
						"token": this.token
					},
					success: (res) => {
						console.log(that.orderId)
						if (res.data.code == 200) {
							uni.navigateTo({
								url: '../confirmMeal/confirmMeal?id=' + that.orderId,
								animationType: 'slide-in-right',
								animationDuration: 200
							});
						} else {
							uni.showToast({
								title: res.data.msg,
								icon: 'none',
								duration: 1000
							})
						}
					}
				})
			} else if (this.type == 3) {
				uni.navigateTo({
					url: '../assess/assess?id=' + that.orderId,
					animationType: 'slide-in-right',
					animationDuration: 200
				});
			} else if (this.type == 4) {
				uni.switchTab({
					url: '../order/order'
				});
			} else if (this.type == 0) {
				uni.request({
					url: this.nowUrl + '/foods/shopcar/remove?id=' + this.orderId,
					header: {
						'token': this.token
					},
					method: 'POST',
					success(res) {
						// console.log(res)
						// uni.navigateTo({
						// 	url: '../index/index'
						// })
						if (res.data.code == 200) {
							uni.switchTab({
								url: '../order/order'
							});
						}else{
							uni.showToast('删除失败')
						}

						// uni.navigateTo({
						// 	url: '../index/index',
						// 	animationType: 'slide-in-right',
						// 	animationDuration: 200
						// });
					}
				})
			}
		},
		//获取订单详情
		getOrderDetail(id) {
			uni.request({
				url: this.nowUrl + '/foods/myOrder/find?id=' + id,
				method: 'GET',
				header: {
					"content-type": "application/json",
					"token": this.token
				},
				success: (res) => {
					// console.log(res.data.data);
					this.detailList = res.data.data.foods;
					this.money = res.data.data.priceAll;
					this.peopleNum = res.data.data.peopleNum;
					this.userName = res.data.data.userName;
					this.mobile = res.data.data.mobile;
					this.orderNum = res.data.data.orderNum;
					if (res.data.data.makeTime) {
						this.makeTime = res.data.data.makeTime;
					} else {
						this.makeTime = '! 订单未完成';
					}
					if (res.data.data.remarks) {
						this.remarks = res.data.data.remarks;
					}

				}
			});
		}
	}
}
